<template>
  <div class="goodscontent">
    <el-form :model="form" label-width="100px" :rules="rules" ref="form">
      <el-form-item label="商品图片" prop="banner">
        <el-upload
          class="upload-demo"
          action="/api/admin/upload"
          :on-remove="handleRemove"
          multiple
          :limit="5"
          :on-exceed="handleExceed"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
          :file-list="fileList"
          ref="upload"
          list-type="picture"
        >
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过1500kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="商品信息" prop="message">
        <el-row v-for="(item,index) in messageArray" :key="index" style="height:50px">
          <el-col :span="10">
            <el-form-item label="属性名:" label-width="60px">
              <el-input v-model="item.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="属性值:" label-width="60px">
              <el-input v-model="item.value"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="3" :offset="1">
            <el-button
              type="primary"
              icon="el-icon-plus"
              circle
              size="small"
              @click="handleAdd(index)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-minus"
              circle
              size="small"
              @click="handleDelete(index)"
            ></el-button>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label="商品详情" prop="content">
        <div ref="wangEditor"></div>
      </el-form-item>
      <el-form-item label="配送模板 " prop="did">
        <el-select v-model="form.did">
          <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button @click="handleSumit" type="primary">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import wangEditor from "wangeditor";
import qs from "qs";
export default {
  name: "",
  data() {
    return {
      // 用于提交或者编辑的表单数据
      form: {
        gid: this.gid,
        banner: "",
        message: "",
        content: "",
        did: ""
      },
      // 表单的校验规则
      rules: {
        banner: { required: true, message: "请传入商品轮播图" },
        message: { required: true, message: "请传入商品信息" },
        content: { required: true, message: "请传入商品详情描述" },
        did: { required: true, message: "请选择配送模板" }
      },
      // 表示商品详细信息的数组
      messageArray: [{ name: "", value: "" }],
      // 下拉列表的选项
      options: [],
      // 富文本编辑器的变量  用来保存富文本编辑器的对象
      e: null,
      // 默认的图片列表
      fileList: [],
      // 如果是做修改 id就是修改的项id值
      id: 0
    };
  },
  props: {
    // 接收的一个属性 表示的是当前对应的商品
    gid: {
      type: Number,
      required: true
    }
  },
  components: {},
  created() {},
  mounted() {
    console.log(this.gid);
    // 初始化整个内容区域
    this.initContent();
    // 获取所有的配送模板列表
    this.fetchTemplateData();
  },
  methods: {
    // 获取配送模板信息
    fetchTemplateData() {
      this.$http.get("/dTemplate").then(res => {
        if (res.data.code == 200) {
          this.options = res.data.data.map(v => ({ id: v.id, name: v.name }));
        }
      });
    },
    // 创建富文本编辑器
    createEdit() {
      if (this.e)    return;
      this.e = new wangEditor(this.$refs.wangEditor);
      this.e.customConfig.uploadImgServer = "/api/admin/editUpload";
      this.e.customConfig.uploadFileName = "file";
      this.e.customConfig.onblur = html => {
        if (html !== "<p><br></p>") {
          this.form.content = this.e.txt.html();
        }
        this.$refs.form.validateField("content");
      };
      this.e.create();
    },
    // 移除图片的操作 删除图片列表中的图片时候的钩子函数
    handleRemove(file, fileList) {
      this.form.banner = fileList.map(v => v.response).join(";");
      this.$refs.form.validateField("banner");
    },
    // 图片提示信息
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 5 个图片，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    // 图片数量超出限制的钩子函数
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    // 图片上传成功的钩子函数
    handleAvatarSuccess(res, file, fileList) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.form.banner = fileList.map(v => v.response).join(";");
      this.$refs.form.validateField("banner");
    },
    // 图片上传之前的一个格式验证函数
    beforeAvatarUpload(file) {
      // const isJPG = file.type === "image/jpeg";
      const isJPG =
        ["image/jpeg", "image/jpg", "image/png"].indexOf(file.type) !== -1;
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传图片只能是 JPG JPEG PNG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传商品轮播图不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 添加属性 属性值  添加一条商品信息
    handleAdd(index) {
      // this.messageArray.push({name:"",value:""})
      this.messageArray.splice(index + 1, 0, { name: "", value: "" });
    },
    // 删除属性 属性值 删除一条商品信息
    handleDelete(index) {
      if (this.messageArray.length === 1) {
        this.$message.error("商品至少包含一条描述信息");
        return;
      }
      this.messageArray.splice(index, 1);
    },
    // 提交所有数据  提交操作
    handleSumit() {
      if (this.e.txt.html() !== "<p><br></p>") {
        this.form.content = this.e.txt.html();
      }
      this.$refs.form.validateField("content");
      this.$refs.form.validate(v => {
        if (v) {
          if (this.id) {
            console.log(this.id);
            this.$http.put("/Detail/" + this.id, this.form).then(res => {
              if (res.data.code === 200) {
                this.$message.success("修改成功");
                this.$emit("success");
              }
            });
          } else {
            this.$http.post("/Detail", this.form).then(res => {
              if (res.data.code === 200) {
                this.$message.success("添加成功");
                //  先不清空内容
                // this.clearContent();
                // 把一些操作放到当前dom内容更新:
                this.initContent();
                this.$emit("success");
                // this.$nextTick(()=>{
                // this.$refs.form.clearValidate();
                // this.$refs.form.resetField();
                // })
              }
            });
          }
        } else {
          return false;
        }
      });
    },
    // 初始化页面是空的时候需要清空页面内容
    clearContent() {
      this.form = {
        banner: "",
        message: "",
        content: "",
        did: "",
        gid: this.gid
      };
      this.e.txt.clear();
      this.messageArray = [{ name: "", value: "" }];
      this.$refs.upload.clearFiles();
    },
    // 初始化页面内容的方法
    initContent() {
      // 编辑 根据gid获取
      this.$http.get("/detail", { params: { gid: this.gid } }).then(res => {
        if (res.data.code === 200) {
          this.createEdit();
          if (res.data.data.length > 0) {
            // 当前商品有详情
            let banner = res.data.data[0].banner;
            this.form.banner = banner;
            this.fileList = banner.split(";").map((v, i) => ({
              name: i + 1 + ".jpg",
              url: v,
              response: v
            }));
            let message = res.data.data[0].message;
            this.form.message = message;
            let obj = qs.parse(message);
            let arr = [];
            for (let i in obj) {
              arr.push(obj[i]);
            }
            this.messageArray = arr;
            this.createEdit();
            let content = res.data.data[0].content;
            this.e.txt.html(content);
            this.form.content = content;
            this.form.did = res.data.data[0].did;
            this.id = res.data.data[0].id;
          } else {
            this.clearContent();
            this.$nextTick(v => {
              this.$refs.form.clearValidate();
            });
            this.id = 0;
          }
        }
      });
    }
  },
  watch: {
    // messageArray 发生变化的时候 form.message能够跟着变化
    messageArray: {
      handler() {
        // message数组成转字符串
        this.form.message = qs.stringify(this.messageArray);
        this.$refs.form.validateField("message");
      },
      deep: true
    },
    // 监听gid的变化  只要选择了其它商品 更新form.gid这个值
    gid() {
      console.log(this.gid);
      this.form.gid = this.gid;
      this.initContent();
    }
  }
};
</script>
<style lang='scss' scoped>
</style>
