<template>
  <div class="type">
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/main'}">分类管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类基本信息</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-tabs v-model="activeName">
        <el-tab-pane label="查看" name="show">
          <el-col :span="12">
            <el-tree
              :data="treeData"
              show-checkbox
              node-key="id"
              default-expand-all
              :expand-on-click-node="false"
              :props="defaultProps"
            >
              <span class="custom-tree-node" slot-scope="{ node, data }">
                <span>{{ node.label }}</span>
                <span>
                  <el-button type="primary" size="mini" @click.stop="() => handleEdit(data)">编辑</el-button>
                  <el-button
                    type="danger"
                    size="mini"
                    @click.stop="() => handleDelete(node, data)"
                  >删除</el-button>
                </span>
              </span>
            </el-tree>
          </el-col>
        </el-tab-pane>
        <el-tab-pane label="添加" name="add">
          <el-col :span="12">
            <el-form :model="addForm" label-width="80px" :rules="rules" ref="addForm">
              <el-form-item label="所属分类" prop="pid">
                <el-select v-model="addForm.pid">
                  <el-option
                    v-for="(item,index) in TypeOption"
                    :key="index"
                    :label="item.typename"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="分类名称" prop="typename">
                <el-input v-model="addForm.typename"></el-input>
              </el-form-item>
              <el-form-item label="分类图片" prop="thumb">
                <el-upload
                  class="avatar-uploader"
                  action="api/admin/upload"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                  :before-upload="beforeAvatarUpload"
                >
                  <img v-if="addForm.thumb" :src="addForm.thumb" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="分类排序" prop="sort">
                <el-input-number v-model="addForm.sort"></el-input-number>
              </el-form-item>
              <el-form-item label="是否推荐" prop="status">
                <el-radio label="1" v-model="addForm.status">推荐</el-radio>
                <el-radio label="0" v-model="addForm.status">不推荐</el-radio>
              </el-form-item>
              <el-form-item label="添加栏目">
                <el-button type="primary" @click="handleAdd">添加</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <el-dialog title="编辑信息" :visible.sync="dialogVisible" width="70%">
      <el-form :model="editForm" label-width="80px" :rules="rules" ref="editForm">
        <el-form-item label="所属分类" prop="pid">
          <el-select v-model="editForm.pid">
            <el-option
              v-for="(item,index) in TypeOption"
              :key="index"
              :label="item.typename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分类名称" prop="typename">
          <el-input v-model="editForm.typename"></el-input>
        </el-form-item>
        <el-form-item label="分类图片" prop="thumb">
          <el-upload
            class="avatar-uploader"
            action="api/admin/upload"
            :show-file-list="false"
            :on-success="handleAvatarSuccess2"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="editForm.thumb" :src="editForm.thumb" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="分类排序" prop="sort">
          <el-input-number v-model="editForm.sort"></el-input-number>
        </el-form-item>
        <el-form-item label="是否推荐" prop="status">
          <el-radio label="1" v-model="editForm.status">推荐</el-radio>
          <el-radio label="0" v-model="editForm.status">不推荐</el-radio>
        </el-form-item>
        <el-form-item label="编辑信息">
          <el-button type="primary" @click="handleUpdate()">提交</el-button>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "type",
  data() {
    return {
      activeName: "show",
      treeData: [],
      url: "/type",
      defaultProps: {
        label: "typename"
      },
      addForm: {
        pid: "",
        typename: "",
        sort: "",
        thumb: "",
        status: "0"
      },
      typename: "",
      rules: {
        pid: { required: true, message: "请选择上级栏目", trigger: "change" },
        typename: {
          required: true,
          message: "请输入分类名称",
          trigger: "blur"
        },
        sort: { required: true, message: "请输入排序", trigger: "blur" },
        thumb: { required: true, message: "请选择栏目图片" },
        status: { required: true }
      },
      TypeOption: [],
      thumb: "",
      editForm: {},
      // 弹框是否被看到
      dialogVisible: false
    };
  },
  components: {},
  created() {},
  mounted() {
    this.fetchData();
    this.fetchOption();
  },
  methods: {
    fetchData() {
      // 获取所需数据
      this.$http.get(this.url).then(res => {
        if (res.data.code == 200) {
          let data = [];
          res.data.data.forEach(function(obj) {
            // 是一级栏目 直接添加到数组中
            if (obj.pid == 0) {
              obj.children = [];
              data.push(obj);
            } else {
              // 找对应的一级栏目
              let parent = data.find(o => o.id == obj.pid);
              // 将当前的二级栏目添加到父栏目children中
              parent.children.push(obj);
            }
          });
          this.treeData = data;
        }
      });
    },
    fetchOption() {
      this.$http.get("/type", { params: { pid: 0 } }).then(res => {
        let data = res.data.data;
        data.unshift({ typename: "无上级分类", id: 0 });
        this.TypeOption = data;
      });
    },
    handleAdd() {
      this.$refs.addForm.validate(v => {
        if (v) {
          this.$http.post(this.url, this.addForm).then(res => {
            if (res.data.code == 200) {
              this.$message.success("添加成功");
              this.$refs.addForm.resetFields();
              this.fetchData();
              this.activeName = "show";
              this.fetchOption();
            } else {
              this.$message.error("添加失败");
            }
          });
        }
      });
    },
    handleDelete(node, data) {
      if (!data.hasOwnProperty("children")) {
        this.$http.delete(this.url + "/" + data.id).then(res => {
          this.$message.success("删除成功");
          this.fetchData();
          this.fetchOption();
        });
      } else {
        this.$message.error("该分类下包含子分类，不可删除!");
      }
    },
    handleEdit(obj) {
      this.editForm = obj;
      this.editForm.status = String(this.editForm.status);
      console.log(obj);
      this.dialogVisible = true;
    },
    handleUpdate() {
      this.$refs.editForm.validate(v => {
        if (v) {
          delete this.editForm.create_time;
          delete this.editForm.update_time;
          delete this.editForm.children;
          this.$http
            .put(this.url + "/" + this.editForm.id, this.editForm)
            .then(res => {
              if (res.data.code == 200) {
                this.$message.success("修改成功");
                this.dialogVisible = false;
                this.fetchData();
                this.fetchOption();
              }
            });
        } else {
          this.$message.error("修改失败");
        }
      });
    },
    handleAvatarSuccess(res, file) {
      this.addForm.thumb = res;
      this.$refs.addForm.validateField("thumb");
    },
    handleAvatarSuccess2(res, file) {
      this.editForm.thumb = res;
      this.$refs.editForm.validateField("thumb");
    },
    beforeAvatarUpload(file) {
      //   const isJPG = file.type === "image/jpeg";
      const isJPG =
        ["image/jpeg", "image/jpg", "image/png"].indexOf(file.type) !== -1;
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>
<style lang='scss' scoped>
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  padding-right: 8px;
}
.avatar-uploader .el-upload {
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px dashed #d9d9d9;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
