<template>
  <div class="goods">
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/main'}">商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品基本信息</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 展示商品数据 -->
    <el-card>
      <el-tabs v-model="activeName">
        <el-tab-pane label="查看" name="show">
          <!-- 查询数据  -->
          <el-form :inline="true">
            <el-form-item label="商品名称" label-width="80px">
              <el-input v-model="searchForm.name" placeholder="按名称搜索" clearable></el-input>
            </el-form-item>
            <el-form-item label="价格区间" label-width="80px">
              <el-col :span="12">
                <el-input v-model="searchForm.min_price" placeholder="最低价" clearable></el-input>
              </el-col>
              <el-col :span="12">
                <el-input v-model="searchForm.max_price" placeholder="最高价" clearable></el-input>
              </el-col>
            </el-form-item>
            <el-form-item label="商品分类" label-width="80px">
              <el-select v-model="searchForm.tid" clearable>
                <el-option
                  v-for="item in typeOption"
                  :key="item.id"
                  :label="item.typename"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="上架状态" label-width="80px">
              <el-select v-model="searchForm.state" clearable>
                <el-option
                  v-for="item in stateOption"
                  :key="item.id"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="是否推荐" label-width="80px">
              <el-select v-model="searchForm.recommend" clearable>
                <el-option
                  v-for="item in recomOption"
                  :key="item.id"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-button type="primary" icon="el-icon-search" circle @click="handleSearch"></el-button>
          </el-form>
          <!-- 分割线  -->
          <el-divider></el-divider>
          <!-- 表格展示数据 -->
          <el-table v-bind:data="tableData" style="width:100%">
            <el-table-column label="商品id" prop="id" align="center" width="80px" fixed></el-table-column>
            <el-table-column
              label="商品名称"
              prop="name"
              align="center"
              :show-overflow-tooltip="true"
              fixed
            ></el-table-column>
            <el-table-column label="缩略图" prop="thumb" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.thumb" alt style="height:40px;width:60px" />
              </template>
            </el-table-column>
            <el-table-column label="价格" prop="price" align="center" width="120px">
              <template slot-scope="scope">{{scope.row.price|currency}}</template>
            </el-table-column>
            <el-table-column label="特价" prop="special_price" align="center" width="120px">
              <template slot-scope="scope">{{scope.row.special_price|currency}}</template>
            </el-table-column>
            <el-table-column label="排序" prop="sort" align="center" width="60px"></el-table-column>
            <el-table-column label="分类" prop="tid" align="center" width="60px">
              <template #default="o">{{o.row.type.typename}}</template>
            </el-table-column>
            <el-table-column label="上架状态" prop="state" align="center" width="100px">
              <template slot-scope="scope">{{scope.row.state===1?"上架":"下架"}}</template>
            </el-table-column>
            <el-table-column label="推荐状态" prop="recommend" align="center" width="100px">
              <template slot-scope="scope">{{scope.row.recommend===1?"是":"否"}}</template>
            </el-table-column>
            <el-table-column
              label="创建时间"
              prop="create_time"
              align="center"
              :show-overflow-tooltip="true"
              max-width="120px"
            ></el-table-column>
            <el-table-column
              label="更新时间"
              prop="update_time"
              align="center"
              :show-overflow-tooltip="true"
              max-width="120px"
            ></el-table-column>
            <el-table-column label="操作" align="center" width="200px">
              <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
              </template>
            </el-table-column>
            <el-table-column label="详情管理" align="center">
              <template #default="o">
                <el-button size="mini" @click="handleContentEdit(o.row.id)">详情</el-button>
              </template>
            </el-table-column>
            <el-table-column label="规格管理" align="center">
              <template #default="o">
                <el-button size="mini" @click="handleColorEdit(o.row.id)">规格</el-button>
              </template>
            </el-table-column>
            <el-table-column label="是否上架" align="center">
              <template #default="o">
                <el-switch
                  :value="o.row.state==1"
                  active-color="#13ce66"
                  inactive-color="#ff4949"
                  @click.native="handleStateChange(o.row.id,o.row.state)"
                ></el-switch>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" align="middle" style="height:40px">
            <el-pagination
              background
              :current-page.sync="page"
              :page-sizes="[5, 10, 15, 20]"
              :page-size.sync="pageSize"
              layout="total, sizes, prev, pager, next"
              :total="total"
            ></el-pagination>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="添加" name="add">
          <el-col :span="12">
            <el-form label-width="80px" :rules="rules" :model="addForm" ref="addForm">
              <el-form-item label="商品名称" prop="name">
                <el-input v-model="addForm.name" style="width:300px"></el-input>
              </el-form-item>
              <el-form-item label="商品图片" prop="thumb">
                <!-- 图上上传  -->
                <el-upload
                  class="avatar-uploader"
                  action="api/admin/upload"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                  :before-upload="beforeAvatarUpload"
                >
                  <img v-if="addForm.thumb" :src="addForm.thumb" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="商品价格" prop="price">
                <el-input-number v-model="addForm.price" style="width:300px"></el-input-number>
              </el-form-item>
              <el-form-item label="商品特价" prop="special_price">
                <el-input-number v-model="addForm.special_price" style="width:300px"></el-input-number>
              </el-form-item>
              <el-form-item label="商品排序" prop="sort">
                <el-input-number v-model="addForm.sort" style="width:300px"></el-input-number>
              </el-form-item>
              <el-form-item label="所属分类" prop="tid">
                <!-- <el-autocomplete
                  class="inline-input"
                  v-model="typename"
                  :fetch-suggestions="querySearch"
                  placeholder="请输入商品名称进行搜索"
                  :trigger-on-focus="false"
                  @select="handleSelect"
                ></el-autocomplete>-->
                <el-select v-model="addForm.tid">
                  <el-option
                    v-for="item in typeOption"
                    :key="item.id"
                    :label="item.typename"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
              <!-- <el-form-item label="上架状态" prop="state">
                <el-radio-group v-model="addForm.state">
                  <el-radio-button label="1">上架</el-radio-button>
                  <el-radio-button label="0">下架</el-radio-button>
                </el-radio-group>
              </el-form-item>-->
              <el-form-item label="是否推荐" prop="recommend">
                <el-radio-group v-model="addForm.recommend">
                  <el-radio-button label="1">是</el-radio-button>
                  <el-radio-button label="0">否</el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="handleAdd">添加</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <el-dialog title="编辑信息" :visible.sync="dialogVisible" width="50%">
      <el-form label-width="80px" :rules="rules" :model="editForm" ref="editForm">
        <el-form-item label="商品名称" prop="name">
          <el-input v-model="editForm.name" style="width:450px"></el-input>
        </el-form-item>
        <el-form-item label="商品图片" prop="thumb">
          <!-- 图上上传  -->
          <el-upload
            class="avatar-uploader"
            action="api/admin/upload"
            :show-file-list="false"
            :on-success="handleAvatarSuccessEdit"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="editForm.thumb" :src="editForm.thumb" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="商品价格" prop="price">
          <el-input-number v-model="editForm.price" style="width:450px"></el-input-number>
        </el-form-item>
        <el-form-item label="商品特价" prop="special_price">
          <el-input-number v-model="editForm.special_price" style="width:450px"></el-input-number>
        </el-form-item>
        <el-form-item label="商品排序" prop="sort">
          <el-input-number v-model="editForm.sort" style="width:450px"></el-input-number>
        </el-form-item>
        <el-form-item label="所属分类" prop="tid">
          <el-autocomplete
            class="inline-input"
            v-model="editName"
            :fetch-suggestions="querySearch"
            placeholder="请输入商品名称进行搜索"
            :trigger-on-focus="false"
            @select="handleSelectEdit"
          ></el-autocomplete>
        </el-form-item>
        <el-form-item label="上架状态" prop="state">
          <el-radio-group v-model="editForm.state" prop="state">
            <el-radio-button label="1">上架</el-radio-button>
            <el-radio-button label="0">下架</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否推荐" prop="recommend">
          <el-radio-group v-model="editForm.recommend" prop="recommend">
            <el-radio-button label="1">是</el-radio-button>
            <el-radio-button label="0">否</el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible=false">取消</el-button>
        <el-button type="primary" @click="handleUpdate()">确认</el-button>
      </span>
    </el-dialog>
    <el-dialog title="编辑详情" :visible.sync="dialogVisible2" width="70%">
      <goods-content :gid="gid" @success="dialogVisible2=false"></goods-content>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible2=false">取消</el-button>
        <!-- <el-button type="primary" @click="handleUpdate()">确认</el-button> -->
      </span>
    </el-dialog>
    <el-dialog title="编辑规格" :visible.sync="dialogVisible3" width="70%">
      <goods-color :gid="gid" @success="dialogVisible3=false"></goods-color>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible3=false">取消</el-button>
        <!-- <el-button type="primary" @click="handleUpdate()">确认</el-button> -->
      </span>
    </el-dialog>
  </div>
</template>

<script>
//  商品详情修改
import GoodsEdit from "@/components/GoodsContent.vue";
import ColorEdit from "@/components/GoodsColor.vue";
export default {
  name: "goods",
  data() {
    return {
      // 选项卡
      activeName: "show",
      // 表格数据
      tableData: [],
      // search 字段
      searchForm: {
        name: "",
        min_price: "",
        max_price: "",
        tid: "",
        state: "",
        recommend: ""
      },
      //
      typeOption: [],
      stateOption: [
        { label: "未上架", value: "0" },
        { label: "已上架", value: "1" }
      ],
      recomOption: [
        { label: "否", value: "0" },
        { label: "是", value: "1" }
      ],
      // 请求数据的网址
      url: "/goods",
      // 总条数
      total: 0,
      // 页码
      page: 1,
      // 每页展示的条数
      pageSize: 5,
      // 分类名称
      typename: "",
      // 编辑的时候需要的分类名称
      editName: "",
      // 弹框默认是关闭
      dialogVisible: false,

      dialogVisible2: false,

      dialogVisible3: false,
      // 添加的数据
      addForm: {
        name: "",
        thumb: "",
        price: "",
        special_price: "",
        sort: "",
        tid: "",
        state: "0",
        recommend: "0"
      },
      // 修改的表格
      editForm: {},
      rules: {
        name: { required: true, message: "请输入商品名称", trigger: "blur" },
        thumb: { required: true, message: "请选择图片" },
        price: { required: true, message: "请输入价格", trigger: "blur" },
        special_price: {
          required: true,
          message: "请输入价格",
          trigger: "blur"
        },
        sort: { required: true, message: "请输入排序", trigger: "blur" },
        tid: [
          { required: true, message: "请选择商品分类", trigger: "blur" },
          {
            type: "number",
            message: "请通过选择的方式来指定分类",
            trigger: "blur"
          }
        ],
        // state:{required: true, message:"请选择上架状态"},
        recommend: { required: true, message: "请选择是否推荐" }
      },
      // 传值
      gid: 0
    };
  },
  components: {
    // 商品详情组件
    "goods-content": GoodsEdit,
    "goods-color": ColorEdit
  },
  created() {},
  mounted() {
    // 渲染时候直接获取数据
    this.fetchData();
    this.fetchTypeDate();
  },
  methods: {
    // 请求后台 获取数据
    fetchData() {
      let params = {
        page: this.page,
        pageSize: this.pageSize
      };
      if (this.searchForm.name) {
        params.name = this.searchForm.name;
      }
      if (this.searchForm.min_price && this.searchForm.max_price) {
        params.min_price = this.searchForm.min_price;
        params.max_price = this.searchForm.max_price;
      }
      if (this.searchForm.tid) {
        params.tid = this.searchForm.tid;
      }
      if (this.searchForm.state) {
        params.state = this.searchForm.state;
      }
      if (this.searchForm.recommend) {
        params.recommend = this.searchForm.recommend;
      }
      this.$http
        .get(this.url, {
          params
          // 使用get请求 然后传入数据需要 params传送页面
        })
        .then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
            // this.typename = res.data.tid;
          } else {
            this.$message.error("获取失败");
          }
        });
    },
    // 查询商品分类
    fetchTypeDate() {
      this.$http
        .get("/type", {
          params: {
            level: 2
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.typeOption = res.data.data;
          }
        });
    },
    // 删除操作
    handleDelete(id) {
      this.$confirm("此操作将删除当前商品,是否继续？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$http.delete(this.url + "/" + id).then(res => {
            if (res.data.code == 200) {
              this.$message.success("删除成功");
              this.fetchData();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除 "
          });
        });
    },
    // 图片上传的方法
    handleAvatarSuccess(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.addForm.thumb = res;
      this.$refs.addForm.validateField("thumb");
    },
    // 图片上传的方法
    handleAvatarSuccessEdit(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.editForm.thumb = res;
      this.$refs.editForm.validateField("thumb");
    },
    // 图片上传的格式
    beforeAvatarUpload(file) {
      // const isJPG = file.type === "image/jpeg";
      const isJPG =
        ["image/jpeg", "image/jpg", "image/png"].indexOf(file.type) !== -1;
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传图片只能是 JPG JPEG PNG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 添加操作
    handleAdd() {
      this.$refs.addForm.validate(v => {
        if (v) {
          this.$http.post(this.url, this.addForm).then(res => {
            if (res.data.code == 200) {
              this.$message.success("添加成功");
              this.$refs.addForm.resetFields();
              this.activeName = "show";
              this.fetchData();
            } else {
              this.$message.error("添加失败");
            }
          });
        }
      });
    },
    // 查询商品所属分类
    querySearch(queryString, cb) {
      this.$http
        .get("/type", {
          params: {
            page: 1,
            pageSize: 100,
            name: queryString
          }
        })
        .then(res => {
          let type = res.data.data;
          let options = type.map(v => ({
            value: v.typename,
            id: v.id
          }));
          cb(options);
        });
    },
    // 表格展示数据的分类id
    handleSelectshow(res) {
      this.addForm.tid = res.id;
      this.$refs.addForm.validateField("tid");
    },
    // 查询之后得到的分类id
    handleSelect(res) {
      this.addForm.tid = res.id;
      this.$refs.addForm.validateField("tid");
    },
    // 查询之后得到的商品id
    handleSelectEdit(res) {
      this.editForm.tid = res.id;
      this.$refs.editForm.validateField("tid");
    },
    //  点击编辑获取分类数据以及商品数据操作
    handleEdit(obj) {
      this.editForm = obj;
      this.editForm.state = String(this.editForm.state);
      this.editForm.recommend = String(this.editForm.recommend);
      this.$http.get("/type/" + obj.tid).then(res => {
        if (res.data.code == 200) {
          this.editName = res.data.data.typename;
        } else {
          this.$message.error("数据请求失败");
        }
      });
      this.dialogVisible = true;
    },
    // 编辑商品以后的提交操作
    handleUpdate() {
      this.$refs.editForm.validate(v => {
        if (v) {
          delete this.editForm.create_time;
          delete this.editForm.update_time;
          this.$http
            .put(this.url + "/" + this.editForm.id, this.editForm)
            .then(res => {
              if (res.data.code == 200) {
                this.$message.success("修改成功");
                this.dialogVisible = false;
                this.fetchData;
              } else {
                this.$message.error("修改失败");
              }
            });
        } else {
          this.$message.error("请根据提示输入");
        }
      });
    },
    //  搜索
    handleSearch() {
      this.fetchData();
    },
    // 商品详情组件弹窗
    handleContentEdit(id) {
      this.dialogVisible2 = true;
      this.gid = id;
    },
    handleColorEdit(id) {
      this.dialogVisible3 = true;
      this.gid = id;
    },
    handleStateChange(id, state) {
      if (state == 1) {
        this.$http.put(this.url + "/" + id, { state: 0 }).then(res => {
          if (res.data.code == 200) {
            this.$message.success("下架成功");
            this.fetchData();
          }
        });
      } else {
        let p1 = this.$http.get("/detail", { params: { gid: id } });
        let p2 = this.$http.get("/color", { params: { gid: id } });
        Promise.all([p1, p2]).then(res => {
          if (res[0].data.data.length > 0 && res[1].data.data.length > 0) {
            this.$http.put(this.url + "/" + id, { state: 1 }).then(res => {
              if (res.data.code === 200) {
                this.$message.success("上架成功");
                this.fetchData();
              }
            });
          } else {
            this.$message.error("商品详情或者商品规格还未添加，不能上架");
          }
        });
      }
    }
  },
  // 过滤
  filters: {
    showPrice(price) {
      return "￥" + this.price.toFixed(2);
    }
  },
  watch: {
    // 监测页的变化
    page(newValue, oldValue) {
      this.fetchData();
    },
    // 监测条数的变化
    pageSize(newValue, oldValue) {
      this.fetchData();
    }
  }
};
</script>
<style lang='scss' scoped>
.el-breadcrumb {
  line-height: 60px;
}
.el-card {
  margin-top: 20px;
  margin-left: 20px;
  // height: 450px;
}
.avatar-uploader .el-upload {
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px dashed #d9d9d9;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.el-table {
  margin-top: 30px;
}
.el-form {
  margin-top: 30px;
}
</style>
