<template>
  <div class>
    <!-- 面包屑  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>轮播管理</el-breadcrumb-item>
      <el-breadcrumb-item>轮播管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 展示数据 -->
    <el-card>
      <!-- 选项卡模式 -->
      <el-tabs v-model="activeName">
        <!-- 查看表格列表 -->
        <el-tab-pane label="查看" name="show">
          <el-table :data="tableData">
            <el-table-column label="id" prop="id" align="center"></el-table-column>
            <el-table-column label="缩略图" prop="thumb" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.thumb" alt style="height:40px;width:60px" />
              </template>
            </el-table-column>
            <el-table-column label="创建时间" prop="create_time" align="center"></el-table-column>
            <el-table-column label="修改时间" prop="update_time" align="center"></el-table-column>
            <el-table-column label="状态" prop="status" align="center">
              <template #default="o">{{o.row.status===1?"显示":"隐藏"}}</template>
            </el-table-column>
            <el-table-column label="排序" prop="sort" align="center"></el-table-column>
            <el-table-column label="操作" align="center">
              <template #default="o">
                <el-button type="text" @click="handleEdit(o.row)">编辑</el-button>
                <el-button type="text" @click="handleDelete(o.row.id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" align="middle" style="height:40px">
            <el-pagination
              background
              :current-page.sync="page"
              :page-sizes="[5, 10, 15, 20]"
              :page-size.sync="pageSize"
              layout="total, sizes, prev, pager, next"
              :total="total"
            ></el-pagination>
          </el-row>
          <!-- 添加轮播 -->
        </el-tab-pane>
        <!-- 添加表单操作 -->
        <el-tab-pane label="添加" name="add">
          <el-col :span="12">
            <el-form label-width="80px" :model="addForm" :rules="rules" ref="addForm">
              <el-form-item label="图片" prop="thumb">
                <!-- 图上上传  -->
                <el-upload
                  class="avatar-uploader"
                  action="api/admin/upload"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                  :before-upload="beforeAvatarUpload"
                >
                  <img v-if="addForm.thumb" :src="addForm.thumb" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="对应商品" prop="gid">
                <el-autocomplete
                  class="inline-input"
                  v-model="name"
                  :fetch-suggestions="querySearch"
                  placeholder="请输入商品名称进行搜索"
                  :trigger-on-focus="false"
                  @select="handleSelect"
                ></el-autocomplete>
              </el-form-item>
              <el-form-item label="状态" prop="status">
                <el-radio v-model="addForm.status" label="1" style="display:block">显示</el-radio>
                <el-radio v-model="addForm.status" label="0">隐藏</el-radio>
              </el-form-item>
              <el-form-item label="排序" prop="sort">
                <el-input-number v-model="addForm.sort"></el-input-number>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="handleAdd">添加</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <el-dialog
  title="编辑信息"
  :visible.sync="dialogVisible"
  width="70%">
  <el-form label-width="80px" :model="editForm" :rules="rules" ref="editForm">
              <el-form-item label="图片" prop="thumb">
                <!-- 图上上传  -->
                <el-upload
                  class="avatar-uploader"
                  action="api/admin/upload"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess1"
                  :before-upload="beforeAvatarUpload"
                >
                  <img v-if="editForm.thumb" :src="editForm.thumb" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="对应商品" prop="gid">
                <el-autocomplete
                  class="inline-input"
                  v-model="editname"
                  :fetch-suggestions="querySearch"
                  placeholder="请输入商品名称进行搜索"
                  :trigger-on-focus="false"
                  @select="handleSelect1"
                ></el-autocomplete>
              </el-form-item>
              <el-form-item label="状态" prop="status">
                <el-radio v-model="editForm.status" label="1">显示</el-radio>
                <el-radio v-model="editForm.status" label="0">隐藏</el-radio>
              </el-form-item>
              <el-form-item label="排序" prop="sort">
                <el-input-number  v-model="editForm.sort"></el-input-number>
              </el-form-item>
            </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="handleUpdate()">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  name: "banner",
  data() {
    return {
      // 选项卡默认查看
      activeName: "show",
      // 所以数据
      tableData: [],
      // 地址
      url: "/banner",
      // 商品名称
      name: "",
      // 编辑的时候需要的商品名称
      editname:"",
      // 总条数 
      total: 0,
      // 每页条数
      pageSize: 5,
      // 当前页数
      page: 1,
      thumb: "",
      imageUrl: "",
      // 添加的数据
      addForm: {
        thumb: "",
        gid: "",
        status: "0",
        sort: ""
      },
      rules: {
        thumb: { required: true, message: "请选择图片" },
        gid: [
          { required: true, message: "请选择商品", trigger: "blur" },
          {
            type: "number",
            message: "请通过选择的方式来指定商品",
            trigger: "blur"
          }
        ],
        status: { required: true, message: "请选择状态" },
        sort: { required: true, message: "请输入排序", trigger: "blur" }
      },
      editForm:{ },
      // 弹框是否被看到
      dialogVisible:false,
    };
  },
  mounted() {
    // 进来直接获取数据
    this.fetchData();
  },
  methods: {
    // 获取数据 请求后台
    fetchData() {
      this.$http
        .get(this.url, {
          params: {
            // 用get请求需要使用params里去传送数据 用data的数据
            page: this.page,
            pageSize: this.pageSize
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
          } else {
            this.$message.error("获取失败");
          }
        });
    },
    // 图片上传的方法
    handleAvatarSuccess(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.addForm.thumb = res;
      this.$refs.addForm.validateField("thumb");
    },
    // 编辑图片上传的方法
    handleAvatarSuccess1(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.editForm.thumb = res;
      this.$refs.editForm.validateField("thumb");
    },
    // 图片上传的格式
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      // const isPNG = file.type === "png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 查询商品
    querySearch(queryString, cb) {
      this.$http
        .get("/goods", {
          params: {
            page: 1,
            pageSize: 100,
            name: queryString
          }
        })
        .then(res => {
          let goods = res.data.data;
          let options = goods.map(v => ({
            value: v.name,
            id: v.id
          }));
          cb(options);
        });
    },
    // 查询之后得到的商品id
    handleSelect(res) {
      this.addForm.gid = res.id;
      this.$refs.addForm.validateField("gid");
    },
    // 编辑查询之后得到的商品id
    handleSelect1(res) {
      this.editForm.gid = res.id;
      this.$refs.editForm.validateField("gid");
    },
    // 添加操作
    handleAdd() {
      this.$refs.addForm.validate(v => {
        if (v) {
          this.$http.post(this.url, this.addForm).then(res => {
            if (res.data.code == 200) {
              this.$message.success("添加成功");
              this.$refs.addForm.resetFields();
              this.activeName = "show";
              this.fetchData();
            } else {
              this.$message.error("添加失败");
            }
          });
        }
      });
    },
    // 删除操作
    handleDelete(id) {
      this.$confirm("此操作将删除当前轮播, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$http.delete(this.url + "/" + id).then(res => {
            if (res.data.code === 200) {
              this.$message.success("删除成功");
              this.fetchData();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    //  编辑操作 
    handleEdit(obj){
      this.editForm = obj;
      this.editForm.status = String(this.editForm.status);
      this.$http.get("/goods/"+obj.gid).then(res=>{
        if (res.data.code==200){
          this.editname = res.data.data.name;
        }else {
          this.$message.error("数据获取失败");
        }
      })
      this.dialogVisible = true;
    },
    // 请求服务器编辑数据 成功之后关闭弹出框
    handleUpdate(){
      this.$refs.editForm.validate(v=>{
        if (v){
          delete this.editForm.create_time;
          delete this.editForm.update_time;
          this.$http.put(this.url+"/"+this.editForm.id,this.editForm).then(res=>{
            if (res.data.code==200){
              this.$message.success("修改成功");
              this.dialogVisible = false;
              this.fetchData();
            }
          })
        }else {
          this.$message.error("修改失败");
        }
      })
    }
  },
  watch: {
    // 监测页的变化
    page(newValue, oldValue) {
      this.fetchData();
    },
    // 监测条数的变化
    pageSize(newValue, oldValue) {
      this.fetchData();
    }
  }
};
</script>
<style lang='scss' scoped>
.el-breadcrumb {
  line-height: 60px;
}
.el-card {
  margin-top: 20px;
  margin-left: 20px;
  // height: 450px;
}
.avatar-uploader .el-upload {
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px dashed #d9d9d9;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.el-table{
  margin-top: 30px;
}
.el-form{
  margin-top: 30px;
}
</style>
