<template>
  <div class='color'>
    <el-tabs v-model="activeName">
      <el-tab-pane name="show" label="展示">
        <el-table :data="tableData">
          <el-table-column type="expand">
            <template #default="o">
              <el-col :span="12">
                <el-form label-width="80px" :model="o.row" :rules="rules" ref="editForm">
                  <el-form-item label="名称" prop="name">
                    <el-input v-model="o.row.name"></el-input>
                  </el-form-item>
                  <el-form-item label="颜色" prop="value">
                    <el-input v-model="o.row.value" type="color"></el-input>
                  </el-form-item>
                  <el-form-item label="排序" prop="sort">
                    <el-input-number v-model="o.row.sort" ></el-input-number>
                  </el-form-item>
                  <el-form-item label="库存" prop="store">
                    <el-input-number v-model="o.row.store" ></el-input-number>
                  </el-form-item>
                  <el-form-item label="提交">
                    <el-button type="primary" @click="handleEdit(o.row)">修改</el-button>
                  </el-form-item>
                </el-form>
              </el-col>
            </template>
          </el-table-column>
          <el-table-column label="名称" prop="name"></el-table-column>
          <el-table-column label="颜色" prop="value">
            <template #default="o">
              <div :style="{width:'30px',height:'30px',background:o.row.value,border:'1px solid black'}"></div>
            </template>
          </el-table-column>
          <el-table-column label="排序" prop="sort"></el-table-column>
          <el-table-column label="库存" prop="store"></el-table-column>
          <el-table-column label="操作">
            <template #default="o">
              <el-button size="mini" type="danger" @click="handleDelete(o.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane name="add" label="添加">
        <el-col :span="12">
        <el-form label-width="80px" :model="form" :rules="rules" ref="form">
          <el-form-item label="名称" prop="name">
            <el-input v-model="form.name" clearable  placeholder="请输入规格名称"></el-input>
          </el-form-item>
          <el-form-item label="颜色" prop="value">
            <el-input type="color" v-model="form.value" clearable  placeholder="请选择颜色"></el-input>
          </el-form-item>
          <el-form-item label="排序" prop="sort">
            <el-input-number v-model="form.sort" clearable  placeholder="请输入排序"></el-input-number>
          </el-form-item>
          <el-form-item label="库存" prop="store">
            <el-input-number  v-model="form.store" clearable  placeholder="请输入库存量"></el-input-number>
          </el-form-item>
          <el-form-item label="添加">
            <el-button type="primary" @click="handleAdd">添加</el-button>
          </el-form-item>
        </el-form>
        </el-col>
        </el-tab-pane> 
      </el-tabs>
  </div>
</template>

<script>
export default {
  name: 'color',
  data(){
    return {
      activeName:'show',
      // 表格的数据
      tableData:[],
      // 地址
      url:"/color",
      // 添加的表单
      form:{
        name:'',
        value:'',
        sort:'',
        store:'',
        gid:this.gid
      },
      editForm:{},
      rules: {
        name:{required:true,message:"请添加规格名称",trigger:"blur"},
        value:{required:true,message:"请添加规格颜色",trigger:"blur"},
        sort:{required:true,message:"请添加规格排序",trigger:"blur"},
        store:{required:true,message:"请添加规格库存",trigger:"blur"}
        }
    }
  },
  props:{
    gid: {
      type: Number,
      required: true
    }
  },
  components: {},
  created(){},
  mounted(){
    this.fetchData();
  },
  methods: {
    // 获取商品对应的规格
    fetchData(){
      console.log(this.gid)
      this.$http.get(this.url,{params: {gid:this.gid}}).then(res=>{
        if (res.data.code==200){
          this.tableData = res.data.data;
        }
      })
    },
    // 添加数据操作
    handleAdd(){
      this.$refs.form.validate(v=>{
        if (v){
          this.$http.post(this.url,this.form).then(res=>{
            if (res.data.code==200) {
              this.$message.success("添加成功");
              this.fetchData();
              // 清空表单数据
              this.$refs.form.resetFields();
              this.$refs.form.value = '';
              this.$refs.form.sort  = '';
              this.$refs.form.store = '';
              // 跳转到查看表格
              this.activeName = "show";
              // 重新获取数据 
            }
          })
        }else {
              this.$message.error('添加失败')
        }
      })
    },
    handleDelete(id){
      this.$confirm("此操作将删除当前规格,是否继续？","提示",{
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      }).then(()=>{
        this.$http.delete(this.url+"/"+id).then(res=>{
          if (res.data.code ==200){
            this.$message.success("删除成功");
            // 重新获取数据
            this.fetchData();
          }
        });
      }).catch(() => {
        this.$message({
          type:"info",
          message:"已取消删除 "
        });
      });
    },
    handleEdit(obj){
      console.log(obj);
      this.$refs.editForm.validate(v=>{
        if (v){
          delete obj.create_time;
          delete obj.update_time;
          this.$http.put(this.url+'/'+obj.id,obj).then(res=>{
            if (res.data.code ==200){
              this.$message.success("修改成功");
              this.fetchData;
            }else {
              this.$message.error("修改失败");
            }
          })
        }else {
          this.$message.error("请根据提示输入");
        }
      });
      
    }
  },
  watch: {
     gid() {
      console.log(this.gid);
      this.form.gid = this.gid;
      this.fetchData()
    }
  }
}
</script>
<style lang='scss' scoped>
</style>
